      position: absolute;
      display: flex;
      align-items: center;
      top: 50%;
      right: 20px;
      transform: translateY(-50%);
      transition: all 2s ease 0.3s; }
      .nav-header__icon::before {
        content: '';
        width: 100%;
        position: absolute;
        top: 0;
        border: 1px solid #34547a;
        transition: all 0.2s ease; }
      .nav-header__icon::after {
        content: '';
        width: 100%;
        position: absolute;
        bottom: 0;
        border: 1px solid #34547a;
        transition: all 0.2s ease; }
      .nav-header__icon span {
        display: block;
        width: 100%;
        border: 1px solid #34547a; }
      .nav-header__icon.active::before {
        top: 50%;
        transform: rotate(45deg) translateY(-50%); }
      .nav-header__icon.active::after {
        bottom: 50%;
        transform: rotate(-45deg) translateY(50%); }
      .nav-header__icon.active span {
        border: none; } }

